@import url('~uikit/dist/css/uikit.css');
@import url('~sweetalert2/dist/sweetalert2.css');
@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700');

* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Source Sans Pro', sans-serif;
}

body {
   background: #159957;  /* fallback for old browsers */
   background: -webkit-linear-gradient(to left, #155799, #159957);  /* Chrome 10-25, Safari 5.1-6 */
   background: linear-gradient(to left, #155799, #159957); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
   height: 100vh;
   
}

.container {
   margin: 0 auto;
}

.header {
   min-height: 50px;
   padding: 20px;
   margin: 0 auto;
   background: white;
}

.title {
   text-transform: uppercase;
   font-weight: 500;
}
.margin-button-search {
   margin-top: 23px;
}

.card-container {
   text-align: center;
}


.card {
   
   margin: 20px auto;
}
